<template>
    <div>
        <div class="container">
            <Breadcrumbs :breadcrumbs="breadcrumbs" />
            <h1 class="no-top-margin">Afspraak maken</h1>
            <p>Maak gemakkelijk een afspraak met het formulier hieronder.</p>

            <div class="full-width flex">
                <div class="container-appointment padding-6em">
                    <div class="form">
                        <h3>Voornaam</h3>
                        <div class="full-width">
                            <input :value="firstname" @input="val => firstname = val.target.value" placeholder="Voornaam" class="input">
                        </div>
                        <h3>Achternaam</h3>
                        <div class="full-width">
                            <input :value="lastname" @input="val => lastname = val.target.value" placeholder="Achternaam" class="input">
                        </div>
                        <h3>E-mailadres</h3>
                        <div class="full-width">
                            <input type="email" :value="email" @input="val => email = val.target.value"
                                placeholder="E-mailadres" class="input">
                        </div>
                        <h3>Telefoonnummer</h3>
                        <div class="full-width">
                            <input type="phone" :value="phone" @input="val => phone = val.target.value"
                                placeholder="Telefoonnummer" class="input">
                        </div>
                        <h3>
                            Datum & Tijd
                        </h3>
                        <div class="full-width date-box-outline">
                            <div class="flex space full-width">
                                <img src="../../assets/svg/previous.svg"
                                    :class="chosenDate > 0 ? 'icon-increment left-icon' : 'icon-increment disappear-date left-icon'"
                                    @click="changeDate(-1);">
                                <div class="current-date fixed-width-300">{{ dateShow(dates[chosenDate].appointmentDate) }}
                                </div>
                                <img src="../../assets/svg/next.svg"
                                    :class="chosenDate < dates.length - 1 ? 'icon-increment right-icon' : 'icon-increment disappear-date right-icon'"
                                    @click="changeDate(1);">
                            </div>
                            <div class="filler-1em"></div>
                            <div class="date-box" id="dateBox">
                                <div v-for="(slot, index) in dates[chosenDate].slots" :key="slot"
                                    :class="chosenTime === index ? 'date-available date-chosen' : 'date-available'"
                                    @click="chosenTime = index">
                                    {{ slot }}
                                </div>
                                <div v-if="dates[chosenDate].slots.length === 0">
                                    <p>Sorry. Voor deze dag zijn geen plekken beschikbaar.</p>
                                </div>
                            </div>
                        </div>
                        <h3>Opmerkingen (optioneel)</h3>
                        <textarea class="textarea"></textarea>
                        <h3>Algemene voorwaarden</h3>
                        <label>
                            <input type="checkbox" name="acceptTerms">
                            <span>Ik ga akkoord met de <router-link to="/algemene-voorwaarden">Algemene Voorwaaden</router-link></span>
                        </label>
                        <div class="filler-1em"></div>
                        <div class="flex">
                            <div class="site-button-square">AFSPRAAK INPLANNEN</div>
                        </div>
                    </div>
                </div>

                <div class="full-width">
                    <div class="container full-width hours-container">
                        <img src="../../assets/lashroom_logo_full.png" class="icon-smaller">
                        <h2>Openingstijden</h2>
                        <div class="flex">
                            <ul>
                                <li>Maandag</li>
                                <li>Dinsdag</li>
                                <li>Woensdag</li>
                                <li>Donderdag</li>
                                <li>Vrijdag</li>
                                <li>Zaterdag</li>
                                <li>Zondag</li>
                            </ul>

                            <ul>
                                <li class="closed">Gesloten</li>
                                <li>11:30 - 19:00</li>
                                <li>10:00 - 17:00</li>
                                <li>10:00 - 17:00</li>
                                <li>10:00 - 17:00</li>
                                <li class="closed">Gesloten</li>
                                <li class="closed">Gesloten</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
const newDate = new Date();
const MAX_DAYS_FUTURE = 14;
import Breadcrumbs from '../shared/Breadcrumbs.vue';

export default {
    name: "appointment-form",
    components: {
        Breadcrumbs
    }, methods: {
        getAppointments() {
            let xhr = new XMLHttpRequest();
            xhr.onreadystatechange = function () {
                const response = xhr.responseText;
                console.log(response);

                // const responseJson = JSON.parse(response);
                // console.log(responseJson);
            }
            xhr.open("GET", "https://65311d8c4d4c2e3f333c66d8.mockapi.io/appointments");
            xhr.send(null);
        }, getListOfDates() {
            let dates = [];
            let index = 0;
            while (dates.length < MAX_DAYS_FUTURE) {
                let date = new Date(newDate.getFullYear(), newDate.getMonth(), newDate.getDate() + index);
                index++;

                if (date.getDay() == 0 || date.getDay() == 1 || date.getDay() == 6)
                    continue;

                let slots = this.getDaySlots(date.getDay());

                let finalSlots = [];
                for (var i = 0; i < slots.length; i++) {
                    let ir = Math.round(Math.random());
                    if (ir === 1) {
                        finalSlots.push(slots[i]);
                    }
                }

                dates.push({
                    appointmentDate: date,
                    appointmentAvailable: true,
                    slots: finalSlots
                });
            }
            return dates;
        }, dateShow(date) {
            console.log(date);
            let dateString = "";
            let days = ["Zondag", "Maandag", "Dinsdag", "Woensdag", "Donderdag", "Vrijdag", "Zaterdag"];
            let months = ["Januari", "Februari", "Maart", "April", "Mei", "Juni", "Juli", "Augustus", "September", "Oktober", "November", "December"];

            dateString += days[date.getDay()] + " - ";
            dateString += date.getDate() + " ";
            dateString += months[date.getMonth()] + " ";
            dateString += date.getFullYear();

            return dateString;
        }, changeDate(amount) {
            this.chosenDate += amount;
            this.chosenDate = Math.min(this.chosenDate, this.dates.length - 1);
            this.chosenDate = Math.max(this.chosenDate, 0);

            this.chosenTime = -1;

            const dateBox = document.getElementById("dateBox");
            dateBox.style.animationName = "date-animation";
            dateBox.style.animationDuration = "1000ms";
        }, getDaySlots(dayOfWeek) {
            switch (dayOfWeek) {
                case 2:
                    return [
                        "11:30",
                        "12:30",
                        "13:30",
                        "15:00",
                        "16:00",
                        "17:00",
                        "18:00",
                    ];
                case 3: case 4: case 5:
                    return [
                        "10:00",
                        "11:00",
                        "12:00",
                        "13:00",
                        "14:00",
                        "15:00",
                        "16:00"
                    ];
                default:
                    return [];

            }
        }
    },
    created() {
        this.getAppointments();
        document.title = "Lash Room Deventer | Afspraak maken";
    }, data() {
        return {
            firstname: "",
            lastname: "",
            email: "",
            phone: "",
            date: new Date(),
            available_times: [
                "10:00",
                "11:00",
                "12:00",
                "14:00",
                "15:00",
                "16:00",
                "17:00"
            ], dates: this.getListOfDates(),
            chosenDate: 0,
            chosenTime: -1,
            breadcrumbs: [
                {
                    title: "Home",
                    route: "/"
                },
                {
                    title: "Afspraak maken",
                    route: "/afspraak"
                }
            ]
        }
    }
}
</script>

<style scoped>
.filler-1em {
    height: 1em;
}

.center {
    text-align: center;
    justify-content: center;
}

.input {
    border: 0;
    border-bottom: 1px solid var(--color-tint1);
    width: 100%;
    font-family: "Nunito", sans-serif;
    font-size: 16pt;
    box-sizing: border-box;
    padding-left: 0;
}

input:focus {
    outline: 0;
    border-width: 2px;
    border-color: var(--color-tint0);
}

.current-date {
    text-align: center;
    font-family: var(--header-font);
    font-size: 20pt;
}

.container-appointment {
    width: 100%;
    max-width: 760px;
    background-color: #fff;
    text-align: left;
}

.space {
    justify-content: space-around !important;
}

.container {
    padding: 4em;
    box-sizing: border-box;
    text-align: left;
    background-color: var(--color-tint4);
    color: var(--color-dark-tint4);
    width: 100%;
}

.container-gray {
    background-color: var(--color-tint3);
}

.container-dark-gray {
    background-color: var(--color-dark-tint1);
    color: var(--color-tint5);
}

.textarea {
    transition-duration: var(--transition-200ms);
    width: 100%;
    resize: none;
    height: 100px;
    border: 1px solid var(--color-tint3);
    font-family: "Nunito", sans-serif;
    font-size: 12pt;
}

.textarea:focus {
    outline: 0;
    border-color: var(--color-tint1);
}

.date-box {
    transition-duration: var(--transition-500ms);
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
    justify-content: center;
    overflow: hidden;
}

.date-box-outline {
    padding-top: 2em;
    padding-bottom: 2em;
    border: 1px solid var(--color-tint2);
    background-image: url("../../assets/stripes_light.png");
}

.date-available {
    transition-duration: var(--transition-200ms);
    padding: 1em;
    box-sizing: border-box;
    background-color: var(--color-dark-tint2);
    color: white;
    width: 210px;
    text-align: center;
    border: 1px solid transparent;
}

.date-unavailable {
    background-color: var(--color-unavailable);
}

.date-available:hover {
    background-color: #eee;
    border: 1px solid var(--color-tint3);
    color: var(--color-dark-tint2);
    cursor: pointer;
}

.date-available:active {
    transform: scale(110%);
}

.date-chosen {
    background-color: #fff;
    border: 1px solid var(--color-tint3);
    color: var(--color-dark-tint2);
}

.date-chosen:hover {
    background-color: #fff;
    border: 1px solid var(--color-tint3);
    color: var(--color-dark-tint2);
}

.slots {
    position: relative;
    right: 0;
    float: right;
}

.hidden {
    display: none;
}

.icon-increment {
    opacity: 30%;
    transition-duration: var(--transition-200ms);
    width: 32px;
}

.icon-increment:hover {
    opacity: 100%;
    cursor: pointer;
}

.disappear-date {
    opacity: 0;
}

.disappear-date:hover {
    opacity: 0;
    cursor: default;
}

.fixed-width-300 {
    width: 440px;
}

.left-icon:active {
    transform: translateX(-8px);
}

.right-icon:active {
    transform: translateX(8px);
}

@keyframes date-animation {
    0% {
        opacity: 0;
        max-height: 0;
    }

    100% {
        opacity: 1;
        max-height: 300px;
    }
}

.hours-container {
    top: 4em;
    position: sticky;
    height: 400px;
    max-height: 400px;
}

ul {
    padding-right: 4em;
}

ul,
li {
    padding-left: 0;
}

li {
    list-style-type: none;
    padding-bottom: 1em;
}

.icon-smaller {
    width: 30%;
    border-radius: 50%;
    position: absolute;
    float: right;
    right: 0;
}

.closed {
    font-weight: bold;
    color: var(--color-accent3);
}
</style>